import React, { useState } from 'react';
import { Col, Row ,Image,Space, Switch, Card} from 'antd';

const { Meta } = Card;
export default function HomePage() {
	const [mode,setMode]=useState('day');
	const [bkcolor,setBkcolor]=useState('darkslategray');
	const [fontcolor,setFontcolor]=useState('white');
	const handleChangeMode=()=>{
		if(mode==='day'){
			setMode('night');
			setBkcolor('azure');
			setFontcolor('black');
		}else{
			setMode('day');
			setBkcolor('darkslategray');
			setFontcolor('white');
		}
	}
    return (
	<div style={{color:fontcolor,backgroundColor:bkcolor}}>
	<Row>
	<Col span={1}></Col>
	  <Col span={10}>
	  <h2>欢迎来到scriptLin的个人网站!</h2>
	  <p>这个网站用来展示我的作品集以及分享我的技术学习日常</p>
	  </Col>
	  <Col span={9}>
	  <h4>github:  <a href="https://github.com/scriptLin-bjtu" target='_blank'>scriptLin</a></h4>
	  <h4>Email: p1091451463@hotmail.com</h4>
	  </Col>
	  <Col span={4}>
	  <Space direction="vertical" style={{marginTop:'2rem'}}>
	    <Switch checkedChildren="暗色" unCheckedChildren="亮色" defaultChecked onChange={handleChangeMode}/>
	  </Space>
	  </Col>
	</Row>
	
	<div style={{height:"2rem"}}></div>
	<Row>
	<Col span={1}></Col>
	 <Col span={11}>
	 <Card
	     hoverable
	     style={{
	       width: "75%"
	     }}
	     cover={<img alt="pm" src={process.env.PUBLIC_URL+'/assets/puppetmaster.png'} />}
	   >
	     <Meta title="PuppetMaster 自动化工具" description="一个基于electron+opencv+robotjs的自动化工具软件，可以自定义键鼠图色脚本，点击卡片查看更多 " />
	   </Card>
	 </Col>
	 <Col span={11}>
	 <Card
	     hoverable
	     style={{
	       width: "75%"
	     }}
	     cover={<><img alt="example" src={process.env.PUBLIC_URL+'/assets/puppetmaster.png'} /></>}
	   >
	     <Meta title="校园论坛网站" description="一个基于react的前端项目，实现了完整业务流程的校园论坛网站，点击卡片查看更多" />
	   </Card>
	 </Col>
	 <Col span={1}></Col>
	</Row>
	
	<div style={{height:"2rem"}}></div>
	<Row>
	<Col span={1}></Col>
	 <Col span={11}>
	 <Card
	     hoverable
	     style={{
	       width: "75%"
	     }}
	     cover={<img alt="flappybird" src={process.env.PUBLIC_URL+'/assets/puppetmaster.png'} />}
	   >
	     <Meta title="FlappyBird 浏览器扩展" description="一个运行在浏览器的扩展程序，致敬了经典游戏'FlappyBird'，点击该卡牌查看更多 " />
	   </Card>
	 </Col>
	 <Col span={11}>
	 <Card
	     hoverable
	     style={{
	       width: "75%"
	     }}
	     cover={<><img  alt="example" src={process.env.PUBLIC_URL+'/assets/puppetmaster.png'} /></>}
	   >
	     <Meta title="VideoStamp 浏览器扩展" description="一个提高视频浏览体验的浏览器扩展，已经上线edge扩展商店，点击该卡牌查看更多" />
	   </Card>
	 </Col>
	 <Col span={1}></Col>
	</Row>
	</div>
	);
}
